import {memo, ReactElement, useCallback, useRef} from 'react';
import {ImageSourcePropType} from 'react-native';
import {UserApproved} from './UserApproved';
import CommonTags from './CommonTags';
import styled from "styled-components/native";
import {CssView} from "./BaseComponent";
import {Colors} from "../themes/colors";

interface ItemOutFlowProps {
    onOpenDetail?: (id: string) => void;
    id: string;
    iconLeft?: ImageSourcePropType;
    leftComponent?: ReactElement | null;
    title: string;
    subTitle: string;
    images: string[];
    tagTitle: string;
    tagColor: string;
    amount?: string | number;
    time: string;
    isHideLine?: boolean;
}

const CommonItem = memo(function CommonItem(props: ItemOutFlowProps) {
    const {
        tagColor,
        tagTitle = '',
        subTitle = '',
        title,
        amount = 0,
        time,
        iconLeft,
        images,
        onOpenDetail,
        id,
        leftComponent,
        isHideLine,
    } = props;
    const pressInPageX = useRef<number>(0);
    const pressOutPageX = useRef<number>(0);

    const _onOpenDetail = useCallback(() => {
        if (Math.abs(pressOutPageX.current - pressInPageX.current) <= 20) {
            onOpenDetail && onOpenDetail(id);
        }
    }, [onOpenDetail, id, pressOutPageX.current, pressInPageX.current]);


    return (
        <SViewContainer>
            <Container
                activeOpacity={0.6}
                onPress={_onOpenDetail}
                onPressOut={(e: any) => {
                    pressInPageX.current = e.nativeEvent.pageX;
                }}
                onPressIn={(e: any) => {
                    pressOutPageX.current = e.nativeEvent.pageX;
                }}>
                {iconLeft ? (
                    <CssView paddingTop={4}>
                        <SImage source={iconLeft} />
                    </CssView>
                ) : null}
                {leftComponent ? leftComponent : null}
                <CssView flex={1}>
                    <CssView flexDirection={'row'}>
                        <CssView flex={1} marginRight={16}>
                            <STitle numberOfLines={2}>{title}</STitle>
                            <SubTitle numberOfLines={2}>
                                 {subTitle}
                            </SubTitle>
                        </CssView>

                        <CssView alignItems={'flex-end'}>
                            <STextTimeAgo>{time}</STextTimeAgo>
                            <CommonTags titleTag={tagTitle} colorDefault={tagColor} />
                        </CssView>
                    </CssView>

                    <CssView flexDirection={'row'} marginTop={4}>
                        <SUserApproved images={images} vertical={false} />
                        <SText>{amount} </SText>
                    </CssView>
                </CssView>
            </Container>
            {!isHideLine ? <SViewLine /> : null}
        </SViewContainer>
    );
});

const SUserApproved = styled(UserApproved)`
  flex: 1;
`;

const SViewContainer = styled.View`
  background-color: ${Colors.white};
`;

const SText = styled.Text`
  text-align: center;
  align-self: center;
  color: ${Colors.grey1};
`;

const SViewLine = styled.View`
  height: 1px;
  background:${Colors.grey5}
  width: 100%;
  margin-left: 16px;
`;

const STextTimeAgo = styled.Text`
  color: ${(props: any) => Colors.grey3};
  font-size: 12px;
  line-height: 16px;
  padding-left: 16px;
  padding-bottom: 4px;
`;

const Container = styled.TouchableOpacity`
  width: 100%;
  padding: 16px;
  background-color: ${Colors.white};
  flex-direction: row;
`;

const STitle = styled.Text`
  font-weight: 500;
  color: ${p => Colors.grey1};
  font-size: 16px;
  line-height: 20px;
  padding-right: 12px;
`;

const SubTitle = styled.Text`
  color: ${p => Colors.grey2};
  font-size: 14px;
  line-height: 20px;
  padding-top: 4px;
  padding-right: 12px;
`;

const SImage = styled.Image`
  margin-right: 16px;
`;

export default CommonItem;
