import {createBottomTabNavigator} from "@react-navigation/bottom-tabs";
import React from "react";
import {createNativeStackNavigator} from "@react-navigation/native-stack";
import {AppName} from "./AppNameConfig";
import {CssView} from "./components/BaseComponent";
import {SvgSource} from './assets/SvgSource';
import {Colors} from "./themes/colors";
import ExpenseScreen from "./screens/Expenses";
import OutflowScreen from "./screens/OutflowScreen";
import ExpenseReport from "./screens/ExpenseReport";
import Supplier from "./screens/Supplier";

const Tab = createBottomTabNavigator();
const Stack = createNativeStackNavigator();

const _route: any = {
    "Expenses": <CssView width={24} height={24}><SvgSource.iconExpense /></CssView>,
    "Outflows": <CssView width={24} height={24}><SvgSource.iconOutflow /></CssView>,
    "Expense Report": <CssView width={24} height={24}><SvgSource.iconExpenseReport /></CssView>,
    "Supplier": <CssView width={24} height={24}><SvgSource.iconSupplier /></CssView>,
}

const _routeActive: any = {
    "Expenses": <CssView width={24} height={24}><SvgSource.iconExpenseActive /></CssView>,
    "Outflows": <CssView width={24} height={24}><SvgSource.iconOutflowActive /></CssView>,
    "Expense Report": <CssView width={24} height={24}><SvgSource.iconExpenseReportActive /></CssView>,
    "Supplier": <CssView width={24} height={24}><SvgSource.iconSupplierActive /></CssView>,
}


const BottomTabs = () => {
    return (
        <Tab.Navigator
            screenOptions={({route}) => ({
                tabBarActiveTintColor: Colors.primary,
                tabBarInactiveTintColor: Colors.grey3,
                headerShown: false,
                tabBarIcon: ({focused}) => {
                    if (focused) {
                        return _routeActive[route.name]
                    }
                    return _route[route.name]
                }
            })}>
            <Tab.Screen name="Expenses" component={ExpenseScreen}/>
            <Tab.Screen name="Outflows" component={OutflowScreen}/>
            <Tab.Screen name="Expense Report" component={ExpenseReport}/>
            <Tab.Screen name="Supplier" component={Supplier}/>
        </Tab.Navigator>
    );
};


const Main = () => {
    return (
        <Stack.Navigator>
            <Stack.Screen
                name={`${AppName}-BottomTabs`}
                component={BottomTabs}
                options={{headerShown: false}}
            />
            <Stack.Screen name={`${AppName}-Detail`} component={ExpenseScreen} />
        </Stack.Navigator>
    );
};

export default Main;
