import React, {memo} from 'react';
import {Image, View, ViewStyle} from 'react-native';
import FastImage from 'react-native-fast-image';
import styled from "styled-components/native";
import {Colors} from "../themes/colors";

export interface UserApprovedProps {
  images: string[];
  vertical: boolean;
  style?: ViewStyle;
}
export const UserApproved = memo(function ({
  images,
  vertical,
  style,
}: UserApprovedProps) {
  return (
    <SViewContainer style={style}>
      {!vertical ? (
        images.map((item, index) => {
          if (index == 3 && images.length > 4) {
            return (
              <SView key={item}>
                <SText>+{images.length - 3}</SText>
              </SView>
            );
          }
          return (
            <SViewAvatar key={item}>
              <FastImage source={{uri: item}} style={{width: 24, height: 24, borderRadius: 12, borderWidth: 0.3}} />
              {/*{item && !!item.approved && item.approved != 0 && (*/}
              {/*  <SViewContainerStatus>*/}
              {/*    <SImageCheck*/}
              {/*      source={*/}
              {/*        item.approved == 1 ? IC_CHECK_CIRCLE : IC_REJECT_CIRCLE*/}
              {/*      }*/}
              {/*    />*/}
              {/*  </SViewContainerStatus>*/}
              {/*)}*/}
            </SViewAvatar>
          );
        })
      ) : (
        <View />
      )}
    </SViewContainer>
  );
});

const SViewAvatar = styled.View`
  justify-content: center;
  margin-left: 8px;
`;

const SView = styled.TouchableOpacity`
  justify-content: center;
  align-items: center;
  background-color: ${Colors.grey6};
  border-radius: 30px;
  width: 24px;
  height: 24px;
  margin-left: 8px;
`;

const SText = styled.Text`
  font-size: 12px;
  color: ${Colors.grey2};
  align-items: center;
`;

const SViewContainer = styled.View`
  flex-direction: row;
`;
